.formControl {
  display: inline-block;
  width: 100%;
}

.formControl input,
.formControl textarea {
  border: 1px solid #114e40;
}

.formControl.error input,
.formControl.error textarea {
  border: 2px solid red;
  margin-right: 10px;
  margin-bottom: 10px;
}

.formControl.error span {
  vertical-align: top;
  display: inline-block;
  color: white;
  background: red;
  padding: 5px;
  margin-bottom: 10px;
  line-height: .7em;
}